import { Button, LinearProgress } from '@mui/material';
import AppBar from '@mui/material/AppBar';
import Toolbar from '@mui/material/Toolbar';
import Typography from '@mui/material/Typography';
import { useSelector } from 'react-redux';
import { NavLink, useLocation } from 'react-router-dom';

import { appPath } from 'common/routesPath';
import { selectAppStatus, selectIsAuth } from 'common/selectors';
import { logout } from 'features/login/auth-reducer';
import { useAppDispatch } from 'redux/redux-store';
import './header.scss';

export const Header = (): JSX.Element => {
  const dispatch = useAppDispatch();

  const location = useLocation();

  const appStatus = useSelector(selectAppStatus);
  const isAuth = useSelector(selectIsAuth);

  const getLocationText = (path: string): string => {
    switch (path) {
      case appPath.PROFILE:
        return '';
      case appPath.DIALOGS:
        return '';
      case appPath.USERS:
        return '';
      case appPath.NEWS:
        return '';
      case appPath.MUSIC:
        return '';
      case appPath.SETTINGS:
        return '';
      default:
        return '';
    }
  };

  const logoutHandler = (): void => {
    dispatch(logout());
  };

  return (
    <AppBar className="header">
      <Toolbar>
        {isAuth ? (
          <>
            <Typography variant="h6" component="div">
              {getLocationText(location.pathname)}
            </Typography>
            <Button
              disabled={appStatus === 'pending'}
              onClick={logoutHandler}
              size="large"
              component={NavLink}
              to={appPath.LOGIN}
              color="inherit"
              variant="text"
            >
              
            </Button>
          </>
        ) : (
          <Button
            disabled={appStatus === 'pending'}
            component={NavLink}
            to={appPath.LOGIN}
            size="medium"
            color="inherit"
            variant="text"
          >
            
          </Button>
        )}
      </Toolbar>
      {appStatus === 'pending' && <LinearProgress color="success" />}
    </AppBar>
  );
};
